body{
    display: flex;
    align-items: center;
    justify-content: center;
    height:100vh;
  }
  .App {
    font-family: sans-serif;
    text-align: center;
    
  }
  
  .Game {
    position: relative;
    width: 300px;
    height: 300px;
  }
  
  .Game__menu {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    background: rgba(255, 255, 255, 100);
    border-radius: 10px;
    padding: 15px;
  }
  
  .Game__board {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
  
  .Game__board button {
    border: 1px solid #ddd;
    font-size: 4rem;
    line-height: 0;
  }
  